/* Theme Variables
 ------------------------------------------*/
/* Frame Mode Layout
 ------------------------------------------*/
.note-editor {
  border: 1px solid #dee5e7;
  position: relative;
  overflow: hidden;
  /* dropzone */
  /* codeview mode */
  /* fullscreen mode */
  /* statusbar */
}
.note-editor .note-dropzone {
  position: absolute;
  display: none;
  z-index: 100;
  color: lightskyblue;
  background-color: white;
  opacity: 0.95;
  pointer-event: none;
}
.note-editor .note-dropzone .note-dropzone-message {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  font-size: 28px;
  font-weight: bold;
}
.note-editor .note-dropzone.hover {
  color: #098ddf;
}
.note-editor.dragover .note-dropzone {
  display: table;
}
.note-editor.codeview .note-editing-area .note-editable {
  display: none;
}
.note-editor.codeview .note-editing-area .note-codable {
  display: block;
}
.note-editor.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1050;
  /* bs3 modal-backdrop: 1030, bs2: 1040 */
}
.note-editor.fullscreen .note-editable {
  background-color: white;
}
.note-editor.fullscreen .note-resizebar {
  display: none;
}
.note-editor .note-editing-area {
  position: relative;
  overflow: hidden;
  /* editable */
  /* codeable */
}
.note-editor .note-editing-area .note-editable {
  background-color: #fff;
  color: #000;
  padding: 10px;
  overflow: auto;
  outline: none;
}
.note-editor .note-editing-area .note-editable[contenteditable=true]:empty:not(:focus):before {
  content: attr(data-placeholder);
}
.note-editor .note-editing-area .note-editable[contenteditable="false"] {
  background-color: #e5e5e5;
}
.note-editor .note-editing-area .note-codable {
  display: none;
  width: 100%;
  padding: 10px;
  border: none;
  box-shadow: none;
  font-family: Menlo, Monaco, monospace, sans-serif;
  font-size: 14px;
  color: #ccc;
  background-color: #222;
  resize: none;
  /* override BS2 default style */
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  margin-bottom: 0;
}
.note-editor .note-statusbar {
  background-color: #f5f5f5;
}
.note-editor .note-statusbar .note-resizebar {
  padding-top: 1px;
  height: 8px;
  width: 100%;
  cursor: ns-resize;
}
.note-editor .note-statusbar .note-resizebar .note-icon-bar {
  width: 20px;
  margin: 1px auto;
  border-top: 1px solid #dee5e7;
}
/* Air Mode Layout
------------------------------------------*/
.note-air-editor {
  outline: none;
}
/* Popover
------------------------------------------*/
.note-popover .popover {
