.panel-hover {
  position: relative;
  word-wrap: break-word;
  min-height: 100px;
}
.panel-hover:hover {
  color: #fff;
  transition: all 600ms cubic-bezier(0.19, 1, 0.22, 1);
  border-radius: 0 0 70% 0;
  background-color: #f05050;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.05);
}
.panel-hover:hover .panel-hover-body {
  color: #edf1f2;
}
.panel-hover:hover a {
  color: #fff !important;
}
.panel-hover:not(hover) {
  transition: all 1000ms cubic-bezier(0.19, 1, 0.22, 1);
}
.panel-hover .panel-hover-icon {
  position: absolute;
  float: right;
  right: 0;
  padding: 0.5vw 1vw;
  bottom: 0;
  font-size: 2em;
}
.panel-hover .panel-hover-body {
  display: block;
  padding-top: 0.8vw;
  padding-bottom: 0.8vw;
  color: #98a6ad;
  font-size: 0.8em;
}
.panel-hover .panel-hover-title {
  font-size: 1em;
  line-height: 1.1;
  padding-bottom: 5px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}
/*
 * Разрез
*/
.pageOption {
  overflow: hidden;
  position: relative;
  width: 100%;
  min-height: 30em;
}
.option,
.option img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.option {
  overflow: hidden;
  position: absolute;
  transform: skewX(-25deg);
  transform-origin: 0% 100%;
}
.option img,
.option:after {
  transform: skewX(25deg);
  transform-origin: inherit;
}
/*
 * Разрез
*/
.bg-img-map {
  background: url('/img/map.jpg') #000 no-repeat;
  background-size: cover;
  background-position: center;
}
/*
 * .breadcrumb
 */
.breadcrumb {
  padding: 15px 0 0;
  margin-bottom: 0;
  list-style: none;
  background-color: inherit;
  font-size: 12px;
  font-weight: 300;
}
.block-items {
  padding: 0;
  height: 200px;
  overflow: hidden;
}
.block-items a {
  display: block;
  height: 100%;
}
.block-items a:hover {
  background: #edf1f2;
  transition: 500ms;
}
.block-items a:not(hover) {
  transition: 500ms;
}
